* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background-color: #f4f4f4;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

#search-box {
  width: 400px;
  border: none;
  border-bottom: 2px solid #4169e1;
  outline: none;
  margin-bottom: 24px;
  padding: 8px;
  font-size: 24px;
  transition: all .3s;
}

.country-list {
  width: 400px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.item {
  cursor: pointer;
  border-bottom: 1px solid #4169e1;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.country-item:hover {
  background-color: #f0f0f0;
}

.flag {
  width: 30px;
  margin-right: 16px;
}

.country-info {
  width: 400px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 24px;
  padding: 16px;
}

.country-name--big {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 500;
}

.country-list__text {
  margin-bottom: 8px;
  font-size: 16px;
}

